import React, {useEffect} from 'react';
import { Formik, Field, Form } from 'formik';

import excelIcon from '../assets/excelIcon.png'
import wordIcon from '../assets/wordIcon.png'
import paintIcon from '../assets/paintIcon.png'
import powerPointIcon from '../assets/powerpointIcon.png'

import cohCell from '../assets/cohCell.png'
import cohCell2 from '../assets/cohCell2.png'
import incCell from '../assets/incCell.png'
import incCell2 from '../assets/incCell2.png'

import { ReactComponent as Arrow } from '../assets/arrow.svg'

export function CustomImportRadio(props) {
    return (
        <div className="custom-radio" >
            <label className='quest-img-wrapper'>
                <Field type='radio' name={`${props.quest}Quest`} value={props.letter}/>
                <div className="custom-radio__label">{props.hasOwnProperty('blocky') ? '' : `${props.letter})${props.var}`}</div>
                {props.hasOwnProperty('source') ? <img className={`${props.hasOwnProperty('imgType') ? props.imgType : 'quest'}-img`} alt='icon' src={props.source}/> : <></> }
            </label>
        </div>
    )
}

export function CustomImportCheckbox(props) {
    return (
        <React.Fragment>
            <label className="custom-big-checkbox quest-img-wrapper">
                <Field type="checkbox" name={`${props.quest}Quest`} value={props.letter}/>
                <span className="custom-big-checkbox__checkbox"></span>
                <span className="labeltext">{props.letter}){props.var}</span>
                {props.hasOwnProperty('source') ? <img className={`${props.hasOwnProperty('imgType') ? props.imgType : 'quest'}-img`} alt='icon' src={props.source}/> : <></> }
            </label>
        </React.Fragment>
    )
}

export function QuestionalTr(props) {
    return (
        <tr className='unpadding-quest' >
            <td>{props.quesNum}</td>
            <td><CustomImportRadio blocky='' var='' quest={`${props.ques}`} letter="" /></td>
            <td><CustomImportRadio blocky='' var='' quest={`${props.ques}`} letter="" /></td>
            <td><CustomImportRadio blocky='' var='' quest={`${props.ques}`} letter="" /></td>
            <td><CustomImportRadio blocky='' var='' quest={`${props.ques}`} letter="" /></td>
        </tr>
    )
}

export default function ExcelBeginQuest() {
    function scrolled() {
		window.scrollTo({
			top: 0,
			left: 0,
			behavior: 'smooth'
		});
	}

    useEffect(() => {
        window.scroll(0, 0)   
    })

    return (
        <div>
            <Formik
                initialValues={{
                    firstQuest: '',
                    secQuest: '',
                    thrQuest: '',
                    fourQuest: [],
                    fivQuest: [],
                    sxQuest: '',
                    sevQuest: '',
                    eighQuest: '',
                    nineQuest: '',
                    tenQuest: '',
                    elevQuest: '',
                    twelQuest: '',
                    thirQuest: '',
                    fourteenQuest: '',
                }}
                onSubmit={values => {
                    let count = 0
                    if (values.firstQuest === '') {count += 1}
                    if (values.secQuest === '') {count += 1}
                    if (values.thrQuest === '') {count += 1}
                    let four = values.fourQuest
                    let fiv = values.fivQuest

                    let timedValue = 0
                    for (let key in four) {
                        if (four[key] === '' || four[key] === '') {
                            timedValue += 0.75
                        } else {timedValue -= 0.75}
                    }
                    if (timedValue > 0) {count += timedValue}

                    let timedValue2 = 0
                    for (let key in fiv) {
                        if (fiv[key] === '' || fiv[key] === '') {
                            timedValue2 += 0.75
                        } else {timedValue2 -= 0.75}
                    }
                    if (timedValue2 > 0) {count += timedValue2}

                    if (values.sxQuest === '') {count += 0.5}
                    if (values.sevQuest === '') {count += 0.5}
                    if (values.eighQuest === '') {count += 0.5}
                    if (values.nineQuest === '') {count += 0.5}

                    if (values.tenQuest === '') {count += 0.5}
                    if (values.elevQuest === '') {count += 0.5}
                    if (values.twelQuest === '') {count += 0.5}
                    if (values.thirQuest === '') {count += 0.5}

                    if (values.fourteenQuest == 16384) {count += 2}

                    console.log(values)
                    alert(count)
                }}>
                <Form>
                    {/* FIRST */}

                    <p className='non-margin'><span className='bold'>1.</span>         Excel?</p>

                    <div className='quest-group'>
                        <CustomImportRadio var='' quest='first' letter="" source={powerPointIcon} />
                        <CustomImportRadio var='' quest='first' letter="" source={excelIcon} />
                        <CustomImportRadio var='' quest='first' letter="" source={wordIcon} />
                        <CustomImportRadio var='' quest='first' letter="" source={paintIcon} />
                    </div>

                    {/* SECOND */}

                    <p className='non-margin'><span className='bold'>2.</span>              ?</p>

                    <div className='quest-group'>
                        <CustomImportRadio var='     ' quest='sec' letter="" />
                        <CustomImportRadio var='    ,   ' quest='sec' letter="" />
                        <CustomImportRadio var='       ' quest='sec' letter="" />
                        <CustomImportRadio var='       ' quest='sec' letter="" />
                    </div>

                    {/* THIRD */}

                    <p className='non-margin'><span className='bold'>3.</span>            ?</p>

                    <div className='quest-group'>
                        <CustomImportRadio var=' Ctrl' quest='thr' letter="" />
                        <CustomImportRadio var=' Enter' quest='thr' letter="" />
                        <CustomImportRadio var=' Shift' quest='thr' letter="" />
                        <CustomImportRadio var=' Tab' quest='thr' letter="" />
                    </div>

                    {/* FOURTH */}

                    <p className='non-margin'><span className='bold'>4.</span>          ?</p>

                    <div className='margins-needed'>
                        <CustomImportCheckbox quest='four' imgType="middle" letter="" source={cohCell} />
                        <CustomImportCheckbox quest='four' imgType="middle" letter="" source={incCell} />
                        <CustomImportCheckbox quest='four' imgType="middle" letter="" source={incCell2} />
                        <CustomImportCheckbox quest='four' imgType="middle" letter="" source={cohCell2} />
                    </div>

                    {/* FIFTH */}

                    <p className='non-margin'><span className='bold'>5.</span>      ()    ?</p>

                    <div className='margins-needed'>
                        <CustomImportCheckbox var='             Shift' quest='fiv' letter="" />
                        <CustomImportCheckbox var='   ,      Ctr   ' quest='fiv' letter="" />
                        <CustomImportCheckbox var='   ,      Shift   ' quest='fiv' letter="" />
                        <CustomImportCheckbox var='            ' quest='fiv' letter="" />
                    </div>

                    {/* SIXTH */}

                    <p><span className='bold'>6.</span>     (14)   ().</p>

                    <div className='questional-block'>
                        <div className='margin-right-needed'>
                            <p><span className='bold'>1)</span>  </p>
                            <p><span className='bold'>2)</span>  </p>
                            <p><span className='bold'>3)</span> </p>
                            <p><span className='bold'>4)</span> Google </p>
                        </div>
                        <div>
                            <p><span className='bold'>)</span>     ,    </p>
                            <p><span className='bold'>)</span>        ,     ,        </p>
                            <p><span className='bold'>)</span>  ,       -    </p>
                            <p><span className='bold'>)</span>    ,      </p>
                        </div>
                    </div>

                    <table className='questional-table'>
                        <tbody>
                            <tr>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                            </tr>
                            <QuestionalTr ques='sx' quesNum='1' />
                            <QuestionalTr ques='sev' quesNum='2' />
                            <QuestionalTr ques='eigh' quesNum='3' />
                            <QuestionalTr ques='nine' quesNum='4' />
                        </tbody>
                    </table>

                    {/* SEVENTH */}

                    <p><span className='bold'>7.</span>         Excel.</p>

                    <div className='margin-right-needed'>
                        <p><span className='bold'>)</span>   </p>
                        <p><span className='bold'>)</span>       (  )</p>
                        <p><span className='bold'>)</span>       </p>
                        <p><span className='bold'>)</span>       ,   ,      </p>
                    </div>

                    <table className='questional-table'>
                        <tbody>
                            <tr>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                            </tr>
                            <QuestionalTr ques='ten' quesNum='1' />
                            <QuestionalTr ques='elev' quesNum='2' />
                            <QuestionalTr ques='twel' quesNum='3' />
                            <QuestionalTr ques='thir' quesNum='4' />
                        </tbody>
                    </table>

                    {/* EIGHT */}

                    <p><span className='bold'>8.</span>  Excel.    ,      Excel.    .</p>

                    <Field type='number' className='open-quest' name="fourteenQuest" />
                    <button className='submit-button' type="submit"></button>
                </Form>
            </Formik>
            <button className="scroll-up-down hide" onClick={scrolled}><Arrow className='arrow-pulled-sec turned' /></button>
        </div>
    );
}