import { useState } from "react"
import { useEffect } from "react"

import { Link } from "react-router-dom";

import { ReactComponent as Arrow } from '../assets/arrow.svg'

import { useAuth } from "hooks/useAuth"
import { useDispatch } from "react-redux"
import { removeUser } from "store/slices/userSlice"

const Heading = (props) => {
	const { isAuth, email } = useAuth()
    const dispatch = useDispatch()

    console.log(isAuth, email)

	const [asideOpen, setAsideOpen] = useState(false);
	const [pulledOpen, setPulledOpen] = useState(false);

	let aside = document.getElementsByTagName('aside');

	function displayAside(event) {
		let button = event.target
		if (!asideOpen) {
			setAsideOpen(true);
			aside[0].classList.add('active-aside');
			button.classList.add('burger-active');
		} else {
			setAsideOpen(false);
			aside[0].classList.remove('active-aside');
			button.classList.remove('burger-active');
		}
	}

	useEffect(() => {
		document.body.addEventListener('mouseup', (event) => {
			if (window.screen.width <= 992) {
			const pathAside = event.composedPath().includes(document.getElementsByClassName('aside-bar')[0]);
			const pathClose = event.composedPath().includes(document.getElementsByClassName('burger')[0]);
			const pathNav = event.composedPath().includes(document.getElementsByClassName('flex-nav')[0]);
			const pathPulled = event.composedPath().includes(document.getElementsByClassName('pulled-in-heading')[0]);
		
			if ((!pathClose && !pathAside) || event.target.className === 'link-route') {
				setAsideOpen(false);
				document.getElementsByClassName('aside-bar')[0].classList.remove('active-aside');
				document.getElementsByClassName('burger')[0].classList.remove('burger-active');
			}  
			if (!pathNav && !pathPulled) { 
				setPulledOpen(false);
				document.getElementsByClassName('arrow-pulled')[0].style.transform = 'rotate(-90deg)'
			}
		}});
	}, [Heading])

	function displayPulled() {
		if (!pulledOpen) {
			setPulledOpen(true)
			document.getElementsByClassName('arrow-pulled')[0].style.transform = 'rotate(0)'
		} else {
			setPulledOpen(false)
			document.getElementsByClassName('arrow-pulled')[0].style.transform = 'rotate(-90deg)'
		}
	}

	return (
		<header>
			<button name="button" className='burger' onClick={displayAside} >
				<span className='burger-inner'></span>
			</button>
			<span className="pulled-in-heading" onClick={displayPulled}><Arrow className='arrow-pulled' /></span>
			<nav className={pulledOpen ? 'flex-nav' : 'header-nav'}>
				<span><Link className="link-route" to="/man-extended/news"></Link></span>
				<span><Link className="link-route" to="/man-extended/faq">faq</Link></span>
				<span><Link className="link-route" to="/man-extended/contacts"></Link></span>
				{!isAuth && <button className="loginB"><Link className="link-route" to="/man-extended/login">Login</Link></button>}
				{!isAuth && <button className="loginB"><Link className="link-route" to="/man-extended/register">Sign up</Link></button>}
				{isAuth && <button className="loginB" onClick={() => {
					dispatch(removeUser())
					localStorage.removeItem('email')
				}}>Log out from {email}</button>}
			</nav>
			{props.children}
		</header>
	)
}

export default Heading
