import { Link } from "react-router-dom";
import { ReactComponent as Arrow } from '../assets/arrow.svg'

import { useState } from 'react';

const Scroll = () => {
  const [accorOne, setAccorOne] = useState(false)
  const [accorTwo, setAccorTwo] = useState(false)
  const [accorThree, setAccorThree] = useState(false)

  function setterOne(event) {
    if (event.target.tagName != 'LI') { // to get ahead of propagation
      if (!accorOne) {
        setAccorOne(true)
        document.getElementsByClassName('arrow')[0].style.transform = 'rotate(-90deg)'
      } else {
        setAccorOne(false)
        document.getElementsByClassName('arrow')[0].style.transform = 'rotate(0)'
      }
    }
  }
 
  function setterTwo(event) {
    if (event.target.tagName != 'LI') { // to get ahead of propagation
      if (!accorTwo) {
        setAccorTwo(true)
        document.getElementsByClassName('arrow')[1].style.transform = 'rotate(-90deg)'
      } else {
        setAccorTwo(false)
        document.getElementsByClassName('arrow')[1].style.transform = 'rotate(0)'
      }
    }
  }

  function setterThree(event) {
    if (event.target.tagName != 'LI') { // to get ahead of propagation
      if (!accorThree) {
        setAccorThree(true)
        document.getElementsByClassName('arrow')[2].style.transform = 'rotate(-90deg)'
      } else {
        setAccorThree(false)
        document.getElementsByClassName('arrow')[2].style.transform = 'rotate(0)'
      }
    }
  }

  return (
    <aside className='aside-bar'>
      <h2><Link className="link-route" to="/man-extended"></Link></h2>
      <ul>
        <li onClick={setterOne}><a className='accorLink' >7 </a><Arrow className='arrow' /></li>
        <li><ul className={accorOne ? 'classUL' : undefined}>
          <li onClick={setterTwo}><a className='accorLink'></a><Arrow className='arrow' /></li>
          <li>
            <ul className={accorTwo ? 'themeUL' : undefined}>
              <li className="non-margin" onClick={setterThree}><a className='accorLink'> 1.   </a><Arrow className='arrow' /></li>
              <li>
                <ul className={accorThree ? 'chapterUL' : undefined}>
                  <li className="non-margin"><Link className="link-route" to="man-extended/excel-begin">  </Link></li>
                  <li className="non-margin"><Link className="link-route" to="man-extended/excel-data">   </Link></li>
                </ul>
              </li>
            </ul>
          </li>
        </ul></li>
      </ul>
    </aside>
  )
}

export default Scroll