import React, {useEffect} from 'react';
import { Formik, Field, Form } from 'formik';

import backButtonBigger from '../assets/backButtonBigger.png'
import closeButtonBigger from '../assets/closeButtonBigger.png'
import confirmButtonBigger from '../assets/confirmButtonBigger.png'
import saveButtonBigger from '../assets/saveButtonBigger.png'

import textTurner from '../assets/textTurner.png'
import fillingData from '../assets/fillingData.png'
import formulaBarArrow from '../assets/formulaBarArrow.png'

import firstDataVar from '../assets/firstDataVar.png'
import secondDataVar from '../assets/secondDataVar.png'
import thirdDataVar from '../assets/thirdDataVar.png'
import fourthDataVar from '../assets/fourthDataVar.png'

import jusLalC from '../assets/jusLalC.png'
import jusLalR from '../assets/jusLalR.png'
import jusRalT from '../assets/jusRalT.png'
import jusRalC from '../assets/jusRalC.png'

import jusLalCDat from '../assets/jusLalCDat.png'
import jusLalRDat from '../assets/jusLalRDat.png'
import jusRalTDat from '../assets/jusRalTDat.png'
import jusRalCDat from '../assets/jusRalCDat.png'

import cohCell from '../assets/cohCell.png'
import cohCell2 from '../assets/cohCell2.png'
import incCell from '../assets/incCell.png'
import incCell2 from '../assets/incCell2.png'

import formatPainter from '../assets/formatPainter.png'

import { ReactComponent as Arrow } from '../assets/arrow.svg'

import { CustomImportRadio, CustomImportCheckbox, QuestionalTr } from './ExcelBeginQuest'

export default function ExcelDataQuest() {
    function scrolled() {
		window.scrollTo({
			top: 0,
			left: 0,
			behavior: 'smooth'
		});
	}

    useEffect(() => {
        window.scroll(0, 0)   
    })

    return (
        <div>
            <Formik
                initialValues={{
                    firstQuest: '',
                    secQuest: '',
                    thrQuest: '',
                    fourQuest: [],
                    fivQuest: [],
                    sxQuest: '',
                    sevQuest: '',
                    eighQuest: '',
                    nineQuest: '',
                    tenQuest: '',
                    elevQuest: '',
                    twelQuest: '',
                    thirQuest: '',
                    fourteenQuest: '',
                }}
                onSubmit={values => {
                    let count = 0
                    if (values.firstQuest === '') {count += 1}
                    if (values.secQuest === '') {count += 1}
                    if (values.thrQuest === '') {count += 1}
                    let four = values.fourQuest
                    let fiv = values.fivQuest

                    let timedValue = 0
                    for (let key in four) {
                        if (four[key] === '' || four[key] === '') {
                            console.log(four[key])
                            timedValue += 0.5
                        } else if (four[key] === '') {
                            timedValue += 0.5
                        } else timedValue -= 0.5
                    }
                    if (timedValue > 0) {count += timedValue}

                    let timedValue2 = 0
                    for (let key in fiv) {
                        if (fiv[key] === '' || fiv[key] === '') {
                            timedValue2 += 0.75
                        } else {timedValue2 -= 0.75}
                    }
                    if (timedValue2 > 0) {count += timedValue2}

                    if (values.sxQuest === '') {count += 0.5}
                    if (values.sevQuest === '') {count += 0.5}
                    if (values.eighQuest === '') {count += 0.5}
                    if (values.nineQuest === '') {count += 0.5}

                    if (values.tenQuest === '') {count += 0.5}
                    if (values.elevQuest === '') {count += 0.5}
                    if (values.twelQuest === '') {count += 0.5}
                    if (values.thirQuest === '') {count += 0.5}

                    if (values.fourteenQuest == ' ') {count += 2}

                    console.log(values)
                    alert(count)
                }}>
                <Form>
                    {/* FIRST */}

                    <p className='non-margin'><span className='bold'>1.</span>      ,      ?
                       .</p>

                    <div className='quest-group'>
                        <CustomImportRadio var='' quest='first' letter="" source={backButtonBigger} />
                        <CustomImportRadio var='' quest='first' letter="" source={closeButtonBigger} />
                        <CustomImportRadio var='' quest='first' letter="" source={confirmButtonBigger} />
                        <CustomImportRadio var='' quest='first' letter="" source={saveButtonBigger} />
                    </div>

                    {/* SECOND */}

                    <p className='non-margin'><span className='bold'>2.</span>      ,            ?
                       .</p>

                    <img alt='text turner' src={textTurner}></img>

                    <div className='quest-group'>
                        <CustomImportRadio var=' ' quest='sec' letter="" />
                        <CustomImportRadio var=' ' quest='sec' letter="" />
                        <CustomImportRadio var=' ' quest='sec' letter="" />
                        <CustomImportRadio var=' ' quest='sec' letter="" />
                        <CustomImportRadio var=' ' quest='sec' letter="" />
                    </div>

                    {/* THIRD */}

                    <p className='non-margin'><span className='bold'>3.</span>     ,   ,     ?
                       .</p>

                    <img alt='filling the data' src={fillingData}></img>

                    <div className='margin-bottom-needed quest-group'>
                        <CustomImportRadio var='' quest='thr' letter="" source={firstDataVar} />
                        <CustomImportRadio var='' quest='thr' letter="" source={secondDataVar}/>
                        <CustomImportRadio var='' quest='thr' letter="" source={thirdDataVar}/>
                        <CustomImportRadio var='' quest='thr' letter="" source={fourthDataVar}/>
                    </div>

                    {/* FOURTH */}

                    <p className='non-margin'><span className='bold'>4.</span>          ,        ?</p>
                    <p>   .</p>

                    <div className='margins-needed'>
                        <CustomImportCheckbox var='  Excel,     ' quest='four' letter="" />
                        <CustomImportCheckbox var='    (  Excel   )         ' quest='four' letter="" />
                        <CustomImportCheckbox var='     ,    .         ' quest='four' letter="" />
                        <CustomImportCheckbox var='   ,        ' quest='four' letter="" />
                    </div>

                    {/* FIFTH */}

                    <p className='non-margin'><span className='bold'>5.</span>   ,      ,       ,  ?</p>
                    <p>   .</p>

                    <div className='margins-needed'>
                        <CustomImportCheckbox var='       ' quest='fiv' letter="" />
                        <CustomImportCheckbox var='           ' quest='fiv' letter="" />
                        <CustomImportCheckbox var='       ' quest='fiv' letter="" />
                        <CustomImportCheckbox var='        ' quest='fiv' letter="" />
                    </div>

                    {/* SIXTH */}

                    <p><span className='bold'>6.</span>     (14)   ().</p>

                    <div className='questional-block'>
                        <div className='margin-right-needed'>
                            <p><span className='bold'>1)</span> <img alt='' src={jusLalC} /></p>
                            <p><span className='bold'>2)</span> <img alt='' src={jusLalR} /></p>
                            <p><span className='bold'>3)</span> <img alt='' src={jusRalT} /></p>
                            <p><span className='bold'>4)</span> <img alt='' src={jusRalC} /></p>
                        </div>
                        <div>
                            <p><span className='bold'>)</span> <img alt='' src={jusRalCDat} /></p>
                            <p><span className='bold'>)</span> <img alt='' src={jusRalTDat} /></p>
                            <p><span className='bold'>)</span> <img alt='' src={jusLalRDat} /></p>
                            <p><span className='bold'>)</span> <img alt='' src={jusLalCDat} /></p>
                        </div>
                    </div>

                    <table className='questional-table'>
                        <tbody>
                            <tr>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                            </tr>
                            <QuestionalTr ques='sx' quesNum='1' />
                            <QuestionalTr ques='sev' quesNum='2' />
                            <QuestionalTr ques='eigh' quesNum='3' />
                            <QuestionalTr ques='nine' quesNum='4' />
                        </tbody>
                    </table>

                    {/* SEVENTH */}

                    <p><span className='bold'>7.</span>           <img src={formatPainter} alt='format-painter' />.</p>

                    <div className='margin-right-needed'>
                        <p><span className='bold'>)</span>       <img src={formatPainter} alt='format-painter' /></p>
                        <p><span className='bold'>)</span>   Esc</p>
                        <p><span className='bold'>)</span>   /    -    </p>
                        <p><span className='bold'>)</span>  ,      </p>
                    </div>

                    <table className='questional-table'>
                        <tbody>
                            <tr>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                            </tr>
                            <QuestionalTr ques='ten' quesNum='1' />
                            <QuestionalTr ques='elev' quesNum='2' />
                            <QuestionalTr ques='twel' quesNum='3' />
                            <QuestionalTr ques='thir' quesNum='4' />
                        </tbody>
                    </table>

                    {/* EIGHT */}

                    <p><span className='bold'>8.</span>    ,             ?       (  ).</p>

                    <img alt='text turner' src={formulaBarArrow}></img>

                    <Field type='text' className='open-quest' name="fourteenQuest" />
                    <button className='submit-button' type="submit"></button>
                </Form>
            </Formik>
            <button className="scroll-up-down hide" onClick={scrolled}><Arrow className='arrow-pulled-sec turned' /></button>
        </div>
    );
}