window.addEventListener('scroll', function(e) {
	let arrowPulled = document.querySelector('.scroll-up-down')

    if (window.pageYOffset > 70) {
		arrowPulled.classList.add('show')
    } else {
		arrowPulled.classList.remove('show')
	}
});

