import './App.css';
import {
  BrowserRouter,
  Routes,
  Route,
  Link
} from "react-router-dom";

import { SignUp } from 'components/SignUp';
import { Login } from 'components/Login';

import Scroll from './components/Scroll.js'
import Heading from './components/Heading'
import HomeText from './components/HomeText.js'
import NewsText from './components/NewsText.js'
import FaQText from './components/FaQText';
import ContactText from './components/ContactText';
import ExcelBeginText from './components/ExcelBeginText';
import ExcelBeginQuest from './components/ExcelBeginQuest';
import ExcelDataText from './components/ExcelDataText';
import ExcelDataQuest from './components/ExcelDataQuest'

import {ReactComponent as DarkIconMoon} from './assets/darkTheme.svg'
import {ReactComponent as LightIconSun} from './assets/lightTheme.svg'

import {useTheme} from './hooks/useTheme.js'
import { useEffect } from 'react';

function App() {
  const {theme, setTheme} = useTheme()

  useEffect(() => {
    if (localStorage.getItem('theme') === 'light') {
      document.getElementsByClassName('dark')[0].style.display = 'none';
      document.getElementsByClassName('light')[0].style.display = 'block';
      setTheme('light')
    }
  })


  function changeTheme(e) {
    e.preventDefault();
    e.target.closest('svg').style.display = 'none';
    const svg = e.target.closest('svg').classList[0];
    if (svg === 'dark') {
      document.getElementsByClassName('light')[0].style.display = 'block';
      setTheme('light') 
      localStorage.setItem('theme', 'light')
    } else if (svg === 'light') {
      document.getElementsByClassName('dark')[0].style.display = 'block';
      setTheme('dark') 
      localStorage.setItem('theme', 'dark')
    }
  }

  return (
    <BrowserRouter>
        <Heading>
          <DarkIconMoon className='dark' alt='dark theme' onClick={changeTheme}></DarkIconMoon>
          <LightIconSun className='light' alt='light theme' onClick={changeTheme}></LightIconSun>
        </Heading>
        <Scroll />
        <div className='justify-div-main'>
          <main>
            <Routes>
              <Route path="/man-extended" element={<HomeText />}/>
              <Route path="/man-extended/excel-begin" element={<ExcelBeginText />}/>
              <Route path="/man-extended/excel-begin-quest" element={<ExcelBeginQuest />}/>
              <Route path="/man-extended/excel-data" element={<ExcelDataText />}/>
              <Route path="/man-extended/excel-data-quest" element={<ExcelDataQuest />}/>
              <Route path="/man-extended/news" element={<NewsText />}/>
              <Route path="/man-extended/faq" element={<FaQText />}/>
              <Route path="/man-extended/contacts" element={<ContactText />}/>
              <Route path="/man-extended/register" element={<SignUp />}/>
              <Route path="/man-extended/login" element={<Login />}/>
            </Routes>
          </main>
        </div>
    </BrowserRouter>
  );
}

export default App;
